<template>
<div class="app">
    <leftMenuDBR />
    <div id="DBRRecommendAdvisor">
        <div>
            <h1>DBR Recommend Advisor</h1>
            <p>
                This is the Recommend Advisor of the DBR
            </p>
        </div>
        <div><button type="button" v-on:click="selectStudent()" style="margin-top:10px">Select Student</button></div>
        <select name="student" v-model="student" size="Number_of_options">
            <option v-for="(student, keyStudent) in students" :value="student" :key="keyStudent">{{ student.firstName }} {{ student.lastName }}</option>
        </select>
        <div id="outlinePlanProgramForm">
            <p>
                Outline of the Plan Program Form will be implemented here
            </p>
        </div>
        <div>
            <select multiple name="advisors" v-model="advisors" size="5" style="margin-top:50px; margin-left:500px">
                <option v-for="(advisor, keyAdvisor) in advisors" :value="advisor" :key="keyAdvisor">{{ advisor.firstName }} {{ advisor.lastName }}</option>
            </select>
            <button type="button" v-on:click="putRecommendedAdvisors()" style="margin-top:10px; margin-left:500px">Recommend</button>
            <div class="response">{{this.response}}</div>
        </div>
    </div>
</div>
</template>

<script>
import leftMenuDBR from './DBRLeftMenu.vue';
import axios from "axios";

    export default {
        components: { leftMenuDBR },
        name: 'DBRRecommendAdvisor',
        data() {
            return {
                advisor: {
                    firstName: "",
                    id: "",
                    lastName: "",
                    userType: ""
                },
                advisors: [],

                student: {
                    firstName: "",
                    id: "",
                    lastName: "",
                    userType: ""
                },
                students: [],
                advisorList: [],
                advisorIds: [],
                studentId: "",
                input: {},
                response: '',
            };
        },
        methods: {  
            clearError: function() {
                this.response = "";
            },
            selectStudent() {
                this.clearError();
                this.input.studentId=this.student.id;
            },
            putRecommendedAdvisors() {
                this.clearError();
                console.log(this.student.id);
                console.log(this.advisors);

                this.advisors.forEach(element => {                    
                    this.advisorIds.push(element.id);
                });
                this.input.advisorIds=this.advisorIds;
                console.log(this.input);
                axios({ method: "PUT", "url": "http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/dbrs/recommendAdvisor", "data": this.input, "headers": { "content-type": "application/json" }  })
                .then(result => {
                    console.log(result.data);
                }).catch(error => {
                    console.log(error.response)
                this.response = `${error.response.status}, ${error.response.data.errorMessage}`
                }
                );
                this.advisorIds=[];
            }
        },

        mounted() {
            axios({ method: "GET", "url": "http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/dbrs/getStudentWithNoAdvisor" }).then(result => {
                    console.log(result.data.studentList);
                    this.students = result.data.studentList
                    
                }, error => {
                    console.error(error);
                });
            axios({ method: "GET", "url": "http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/dbrs/getAdvisors" }).then(result => {
                    console.log(result.data.advisorList);
                    this.advisors = result.data.advisorList
                }, error => {
                    console.error(error);
                });
        },
    }
</script>

<style scoped>
    #DBRRecommendAdvisor {
        width: 500px;
        border: 10px solid #CCCCCC;
        background-color: #FFFFFF;
        margin-top: 10px;
        margin-left: 0px;
        padding: 200px;
    }
    #outlinePlanProgramForm {
        width: 200px;
        border: 1px solid #CCCCCC;
        height: 50px;
        background-color: #FFFFFF;
        margin-top: 2px;
        margin-left: 0px;
        padding: 50px;
    }
.response {
  margin-top:10px;
  color:red;
}
