<template>
<div class="app">
    <left-menu-advisor />

    <div id="AdvisorCompleteProgramPlanForms">
        <div>
            <h1>Advisor Complete Program Plan Forms </h1>
        </div>
        <div><select name="student" v-model="selectedStudent" size="Number_of_options">
            <option v-for="(student, keyStudent) in students" :value="student" :key="keyStudent">{{ student.firstName }} {{ student.lastName }}</option>
        </select><button type="button" v-on:click="getSelectedStudentsInformation()" id="SelectStudentButton">Select Student</button></div>
        
        <div>
            <p>
                Student: {{this.selectedStudent.firstName }} {{this.selectedStudent.lastName }}
            </p>
            <p>
                Program Plan Form Status: {{this.programPlanFormStatus}}
            </p>
            <p>
                Thesis Topic: {{this.thesisTopic}}
            </p>
        </div>
    </div>
</div>
</template>

<script>
import LeftMenuAdvisor from './AdvisorLeftMenu.vue';
import axios from "axios"; 
    export default {
        components: { LeftMenuAdvisor },
        name: 'AdvisorCompleteProgramPlanForms',
        data() {
            return {
                students: [],
                selectedStudent: "",
                response: "",
                programPlanFormStatus: "",
                thesisTopic: "",
            };
        },
        mounted() {
            console.log("User ID: "+this.$store.state.user.id);
            axios({ method: "GET", "url": `http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/advisors/getApprovedStudents/${this.$store.state.user.id}` }).then(result => {
                    console.log(result.data.studentList);
                    if (result.data.studentList != null) {
                        result.data.studentList.forEach(element => {
                        if (element != null) {
                            this.students.push(element);
                        }
                    });
                    }                    
                }, error => {
                    console.error(error.response);
                });
        },
        methods: {  
            clearError: function() {
                this.response = "";
            },
            getSelectedStudentsInformation() {
                this.clearError();
                console.log(this.selectedStudent);
                axios({ method: "GET", "url": `http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/programPlanForm/getByStudent/${this.selectedStudent.id}`}) //This request is not needed -Baran
                .then(result => {
                    console.log(result.data);
                    this.programPlanFormStatus = result.data.programPlanFormStatus;
                }).catch(error => {
                this.response = `${error.response.status}, ${error.response.data.errorMessage}`
                });
                axios({ method: "GET", "url": `http://group5mbs-env-1.eba-pdd3hc5p.us-east-2.elasticbeanstalk.com/mbs/students/${this.selectedStudent.id}`}) //This request is not needed -Baran
                .then(result => {
                    console.log(result.data);
                    this.thesisTopic = result.data.student.thesisTopic;
                }).catch(error => {
                this.response = `${error.response.status}, ${error.response.data.errorMessage}`
                });

            }
        }
    }
</script>

<style scoped>
#AdvisorCompleteProgramPlanForms {
    width: 1000px;
    height: 600px;
    border: 1px solid #CCCCCC;
    background-color: #FFFFFF;
    margin-top: 10px;
    margin-left: auto;
    padding: 0px;
}
#SelectStudentButton {
    margin-top: 10px;
    margin-left: 10px;

}
</style>